#carouselExampleControls{
    width: 100%;
    margin-bottom: 10px;
}

.reader-img-container {
    position: relative;
    padding-bottom: 56.25%; /* 16:9 ratio */
    height: 0;
    overflow: hidden;
    margin-bottom: 30px;
    margin-top: 30px;
}

.reader-img-container img {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 70vh;
}

.blog-title{
    font-family: 'Source Sans Pro', sans-serif;
    font-size: 24px;
    margin-top: 30px;
}

.author-and-publish-date{
    font-family: 'Source Sans Pro', sans-serif;
    font-size: 16px;
}